.rectangle-div {
  position: relative;
  border-radius: var(--br-19xl);
  background-color: var(--color-white);
  width: 172px;
  height: 192px;
}
.frame {
  width: 742px;
  height: 192px;
  overflow: hidden;
  flex-shrink: 0;
  flex-direction: row;
  gap: var(--gap-lg);
}
.frame,
.frame-parent1,
.frame-root {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.frame-parent1 {
  flex: 1;
  height: 831px;
  flex-direction: column;
  gap: 21px;
}
.frame-root {
  position: relative;
  background-color: var(--color-darkslategray);
  width: 100%;
  flex-direction: row;
  padding: 66px var(--padding-45xl) var(--padding-48xl);
  box-sizing: border-box;
  gap: 504px;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-pink);
  font-family: var(--font-poppins);
}
